// 定义 showConfirm 方法
import { Modal } from 'antd';
export const showConfirm = (message: string, title: string = '确认操作'): Promise<void> => {
  return new Promise((resolve, reject) => {
    Modal.confirm({
      title,
      content: message,
      onOk() {
        console.log('ok');
        resolve();
      },
      onCancel() {
        console.log('cancel');
        reject();
      },
    });
  });
};